.input-wrapper, .set {
    border: 2px solid #00eaff;
    border-radius: 5px;
    margin: 5px;
}

.settings-input {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.settings-input input {
    padding-left: 15px;
    width: 80px;
    height: 25px;
    text-align: center;
    font-size: 18px;
    font-weight: 700;
    border: 2px solid #00eaff;
    border-radius: 5px;
}

.set {
    padding: 15px;
}

.settings-input .red {
    border: 2px solid #ff0000;
    background-color: #ffb0aa;

}

.settings-input .red:after {
    position: absolute;
    width: 100%;
    height: 100%;
}